export default {
    data() {
        return {
			index: 0,
			u_phone: '',
			u_name: '',
			array: ['个人POS机', '商用POS机'],
			userList:[]
        }
    },
    methods: {
		bindPickerChange (e) {
		      console.log(e)
			  this.index=e.detail.value
		    },
        onKeyInput: function(event) {
            this.inputValue = event.target.value
        },
        replaceInput: function(event) {
            var value = event.target.value;
            if (value === '11') {
                this.changeValue = '2';
            }
        },
        hideKeyboard: function(event) {
            if (event.target.value === '123') {
                uni.hideKeyboard();
            }
        },
		//确定申请点击事件
		affirmClick: function(){
			if (this.u_name == '') {
				uni.showToast({
					title: '请填写你的姓名',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			if (this.u_phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			console.log(this.u_phone);
			//根据电话号码查询用户信息
			uni.request({
				url: 'http://localhost:8081/getUser?u_phone='+this.u_phone,
				method: 'POST',
				success: res => {
					//console.log(res.data);
					this.userList = res.data;
					uni.request({
						url: 'http://localhost:8081/orderInsert?u_id='+this.userList.uId+"&t_id=3",
						method: 'POST',
						success: res => {
							console.log(res.data);
							this.userList = res.data;
						}
					});
				}
			});
			
			
			uni.reLaunch({
				url: '/pages/correct/correct'
			});
			
		}
    }
}
